.home_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4em;
}
.home_container h1 {
    font-size: 2.5em;
    margin-bottom: .5em;
}
.home_container h1 span {
    color: #FFBB33;
    padding: 0 .2em;
    background-color: rgba(0, 0, 0, 0.822);
}
.home_container p {
    margin-bottom: 1.5em;
    color: #7B7B7B;
}
.home_container img {
    width: 350px;
    margin: 2em 0;
}