.project_details {
    padding: 2em;
}
.project_details h1,
.project_details h2,
.project_details p {
    margin-bottom: 0.5em;
}
.project_details h1 {
    background-color: rgba(0, 0, 0, 0.822);
    color: #FFBB33;
    padding: 0.4em;
}
.project_details span {
    font-weight: bold;
}

.details_container,
.service_form_container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid #7A7A7A;
    margin-bottom: 1.2em;
    padding-bottom: 1.2em;
}

.btn {
    background-color: rgba(0, 0, 0, 0.822);
    color: white;
    padding: 0.5em 1em;
    text-decoration: none;
    transition: 0.5s;
    cursor: pointer;
    max-height: 40px;
    border: none;
}
.btn:hover {
    color: #FFBB33;
}

.project_info {
    width: 100%;
}